export class Armor {
    constructor(
        public type: string,
        public price: string,
        public ac: number,
        public dexModMax: number,
        public dexModMin: number,
        public strReq: number,
        public hide: string,
        public weight: string,
    ) {}

    public get description(): string {
        let msg: string = `${this.type}${this.price}/${this.weight}AC${this.ac}`;
        if (this.dexModMax !== 0) {
            msg += ``;
            if (this.dexModMax !== 99) {
                msg += `${this.dexModMax}`;
            }
        }
        if (this.hide === '') {
            msg += '';
        }
        return msg;
    }
}
