export class Weapon {
    constructor(
        public name: string,
        public category: string,
        public price: string,
        public damage: string,
        public type: string,
        public weight: string,
        public range: string,
        public special: string,
    ) {}

    public get description(): string {
        let msg: string = `${this.category}${this.price}/${this.weight}${this.damage}[${this.type}]`;
        if (this.range !== '') {
            msg += `${this.range}`;
        }
        if (this.special !== '') {
            msg += `${this.special}`;
        }
        return msg;
    }

}
